import React, { PureComponent,Fragment} from 'react'
import IcTag from 'ic-tag'

export default class extends PureComponent {

    render() {
        return (
            <Fragment>
                <div></div>
                <div>
                    <IcTag className="ic-tag-wrapper" color="#11AABB"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#3B99FF"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#FC3333"></IcTag>
                    <IcTag className="ic-tag-wrapper" color="#ffa000"></IcTag>
                </div>
                <div></div>
                <div>
                    <IcTag className="ic-tag-wrapper" color="#ffa000" closable={true}></IcTag>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                    <IcTag className="ic-tag-wrapper" closable={true}></IcTag>
                </div>
            </Fragment>
        );
    }
}



